import React, { useEffect, useState } from 'react';
import s from './ProfileInfo.module.css';

const ProfileStatusWithHooks = (props) => {

    // вызывая вот так, мы говорим что useState(false) это (false) твое стартовое значение
    // useState возвращает массив, в котором 2 элемента. В первом находится само название элемента
    // во втором сидит функция, которая будет изменять это одиночное значение

    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    //этот метод говорит так: закиньте в меня ыункцию, которую я выполню, когда уже произойдет отрисовка
    // потом выполню
    // так же после запятой (, [] ) мы можем указать что не всегда нужно выполнять функцию, а к примеру только 1 раз когда компонента вмонтировалась
    // оно так отработает, но пустой массив не стоит указывать
    // но в данном случае мы хотим чтобы юзЭффект срабатывал каждый раз, когда изменится props.status [props.status]
    // то есть ЮзЭффект завтисит от props.status
    useEffect( () => {
        setStatus(props.status);

    }, [props.status] );

    const activateEditMode = () => {
        setEditMode(true);
    };

    const deactivateEditMode = () => {
        setEditMode(false);
        props.updateStatus(status);
    };

    const onStatusChange = (e) => {
        setStatus(e.currentTarget.value);
    };

    return (
        <div>
            {!editMode &&
                <div>
                    <span onDoubleClick={activateEditMode}> {props.status || '-----'} </span>
                </div>
            }
            {editMode &&
                <div>
                    <input onChange={onStatusChange} onBlur={deactivateEditMode} autoFocus={true} value={status}/>
                </div>
            }
        </div>
    )

    // state = {
    //     editMode: false,
    //     status: this.props.status
    // }

    // // для того чтобы не баиндить и чтобы не потерялся контекст, необходимо использовать стрелочную функцию
    // // позже сделали без стрелочной для того чтобы я мог остановиться дебаггером и забаиндили this.activateEditMode.bind(this)
    // // после опять сделали стрелочную
    // activateEditMode = () => {
    //     this.setState({
    //         editMode: true
    //     })
    //     // this.state.editMode = true;
    //     // //теперь метод который тут нел стоит использовать, он для других сценариев
    //     // this.forceUpdate();
    // }

    // deactivateEditMode = () => {
    //     this.setState({
    //         editMode: false
    //     });
    //     this.props.updateStatus(this.state.status);
    // }

    // onStatusChange = (e) => {
    //     this.setState({
    //         status: e.currentTarget.value
    //     });
    // }

    // // вызываются предыдущие пропсы и стейты, которые были до момента обновления
    // componentDidUpdate(prevProps, prevState) {
    //     if (prevProps.status !== this.props.status) {
    //         this.setState({
    //             status: this.props.status
    //         });
    //     }
    // }


}

export default ProfileStatusWithHooks;