import React from 'react';
import { connect } from 'react-redux';
import { getUserProfile, getStatus, updateStatus } from '../../redux/profile-reducer';
import Profile from './Profile';
import { Navigate, useParams } from 'react-router-dom';
// import { withAuthRedirect } from '../hoc/withAuthRedirect';
import { compose } from 'redux';

export function withRouter(Children) {
    return (props) => {

        const match = { params: useParams() };
        return <Children {...props} match={match} />
    }
}


class ProfileContainer extends React.Component {

    componentDidMount() {
        let userId = this.props.match.params.userId;
        // debugger
        if (!userId) {
            // userId = 25464;
            userId = this.props.authorizedUserId;
            if (!userId) {
                // this.props.history.push('/login');
                // !!!!!!!!!!! тут нужно найти способ редиректиться на '/login';


                // так как ниже нельзя делать - нельзя в классах вызывать
                // const navigate = useNavigate();
                // navigate('/login');

            }
        }
        
        this.props.getUserProfile(userId);
        // profileApi.getUserById(userId)
        //     .then(response => {
        //         this.props.setUserProfile(response.data);
        //     });
        this.props.getStatus(userId);
    };

    // const state = props.store.getState().profileReducer;
    render() {

        return (
            <Profile {...this.props} 
                     profile={this.props.profile} 
                     status={this.props.status}
                     updateStatus={this.props.updateStatus} />
        )
    };
};



// const AuthRedirectComponent = withAuthRedirect(ProfileContainer);
const mapStateToProps = (state) => {
    return {
        profile: state.profilePage.profile,
        status: state.profilePage.status,
        authorizedUserId: state.auth.userId,
        isAuth: state.auth.isAuth
    }
};
// // вернет новуб компоненту, которая отрисует ProfileContainer и в нее закинутся данные из УРЛа
// const WithUrlDataContainerComponent = withRouter(AuthRedirectComponent);
// // возвращает новую компоненту, которая отрисует ProfileContainer и закинет в нее данные из стора
// export default connect(mapStateToProps, { getUserProfile })(WithUrlDataContainerComponent);


export default compose(
    connect(mapStateToProps, { getUserProfile, getStatus, updateStatus }),
    withRouter,
    // withAuthRedirect
)
(ProfileContainer);