import React from 'react';
import s from './MyPosts.module.css';
import Post from './Post/Post';

import { Field, reduxForm } from "redux-form";
import { maxLengthCreator, required } from '../../../utils/validators/validators';
import { Textarea } from '../../common/FormsControls/FormsControls';


// React.memo - для того чтобы не перерисовывать компоненту постоянно, а лишь при изменениях пропсов или стэйта
// но пишут что это уже делать не нужно
const MyPosts = React.memo(props => {

  console.log('RENDER MY POST');

  const postsElements = props.posts.map(p => <Post message={p.message} key={p.id} likesCount={p.likesCount} />)

  const newPostElement = React.createRef();

  let onAddPost = (values) => {
    // здесь получаем все данные с формы
    props.addPost(values.newPostText);
  }

  // return(
  //   <div className={s.postsBlock}>
  //     <h3>My posts</h3>
  //     <form onSubmit={props.handleSubmit}>
  //       <div>
  //         <Field  name={'postText'} component={'input'} />
  //       </div>
  //       <div>
  //         <button>Add post</button>
  //       </div>
  //     </form>
  //     <div className={s.posts}>
  //       {postsElements}
  //     </div>
  //   </div>
  // )


  return (
    <div className={s.postsBlock}>
      <h3>My posts</h3>
      <AddNewPostFormRedux onSubmit={onAddPost} />
      <div className={s.posts}>
        {postsElements}

      </div>
    </div>
  )
});

const maxLengthCreator10 = maxLengthCreator(10);

const AddNewPostForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div>
        <Field name='newPostText' component={Textarea} validate={[required, maxLengthCreator10]} placeholder='Post message' />
        {/* <textarea onChange={onPostChange} value={props.newPostText} ref={newPostElement}></textarea> */}
      </div>
      <div>
        <button>Add post</button>
      </div>
    </form>
  )
}

const AddNewPostFormRedux = reduxForm({form: 'ProfileAddNewPostForm'})(AddNewPostForm);

export default MyPosts;